@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100vh;
  font-family: "Josefin Sans", sans-serif;
  background-color: hsl(235, 21%, 11%);
  background-image: url("./images/bg-desktop-dark.jpg");
  background-repeat: no-repeat;
  font-size: 18px;
  display: flex;
  justify-content: center;
}

.container {
  width: 100%;
  max-width: 650px;
  /* background-color: yellow; */
  margin-top: 50px;
}

.header {
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  margin-bottom: 20px;
}

.title {
  color: white;
  font-size: 50px;
  letter-spacing: 10px;
  font-weight: bold;
}

.new-todo {
  background-color: hsl(235, 24%, 19%);
  height: 70px;
  display: flex;
  border-radius: 6px;
}

.check {
  /* background-color: yellow; */
  width: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.check-mark {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 2px solid hsl(237, 14%, 26%);
  cursor: pointer;
  /* .25s is a good standard for transitions */
  transition: all 0.25s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.check-mark:hover {
  background: linear-gradient(135deg, #55ddff 0%, #c058f3 100%);
}

/* #1 Add missing css for active states */
.check-mark.checked {
  background-color: pink;
  background: linear-gradient(135deg, #55ddff 0%, #c058f3 100%);
}

.todo-text.checked {
  text-decoration: line-through;
  color: hsl(233, 14%, 35%);
}

.check-mark.checked img,
.check-mark:hover img {
  visibility: visible;
}

.new-todo-input {
  /* background-color: orange; */
  /* Grow element as much as it can */
  flex-grow: 1;
  display: flex;
  align-items: center;
}

.new-todo-input form {
  flex-grow: 1;
}

.new-todo-input input {
  width: 100%;
  padding: 10px 0px;
  background-color: transparent;
  border: none;
  outline: none;
  color: hsl(234, 39%, 85%);
  font-size: 18px;
}

.todo-items-wrapper {
  margin-top: 30px;
  background-color: hsl(235, 24%, 19%);
  border-radius: 6px;
  overflow: hidden;
}

.todo-item {
  width: 100%;
  height: 70px;
  border-bottom: 1px solid hsl(237, 14%, 26%);
  display: flex;
  transition: all 0.25s ease;
}

.todo-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 0px 7px 0px hsl(235, 21%, 11%);
}

.todo-text {
  display: flex;
  align-items: center;
  font-size: 18px;
  color: hsl(234, 39%, 85%);
}

.todo-items-info {
  height: 70px;
  width: 100%;
  color: hsl(233, 14%, 35%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 30px;
}

.item-statuses {
  display: flex;
  justify-content: center;
}

.item-statuses span {
  padding: 0 10px;
  cursor: pointer;
}

.items-clear span {
  cursor: pointer;
}

.items-clear span:hover {
  color: hsl(220, 98%, 61%);
}

.item-statuses .active {
  color: hsl(220, 98%, 61%);
}

.item-statuses span:hover {
  color: hsl(220, 98%, 61%);
}
